import React from "react";
import ProductCard from "./ProductCard.js";

const ProductList = (props) => {
  return (
    <div className="d-flex justify-content-center container w-100">
      <div className="row w-100 d-flex justify-content-center">
        {props.filtered_products.map((product, i) => {
          return (
            <div className="d-flex justify-content-center col-xs-12 col-sm-6 col-lg-4 col-xl-3 col-padding">
              <ProductCard
                key={i}
                name={product.name}
                description={product.description}
                img_filename={product.img_filename}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default ProductList;
