import React from "react";
import ProductList from "../components/ProductList.js";
import SearchBar from "../components/SearchBar.js";
import { products } from "./product_list.js";
import "tachyons";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "./App.css";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      products: [],
      filtered_products: [],
    };
    this.setState({});
  }

  componentDidMount() {
    this.setState({
      products: products,
      filtered_products: products,
    });
  }

  onSearchButtonClick = (event) => {
    const current_query = document.querySelector("#search-field").value;

    const filtered_products = products.filter((product) => {
      return product.name.toLowerCase().includes(current_query.toLowerCase());
    });
    this.setState({ filtered_products: filtered_products });
  };

  render() {
    return (
      <div>
        <nav className="navbar sticky-top navbar-expand-lg navbar-dark navbar-bg text-dark d-flex align-items-center">
          <table>
            <tbody>
              <tr>
                <td className="align-middle">
                  <img src="logo.png" height="50px" alt="" />
                </td>
                <td className="align-middle">
                  <p
                    id="brand-name"
                    className="brand-name-extra font-weight-bold"
                  >
                    محصولات خشک ماهور
                  </p>
                </td>
              </tr>
            </tbody>
          </table>

          <SearchBar onSearch={this.onSearchButtonClick} />

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#fullMenu"
            aria-controls="fullMenu"
            aria-expanded="true"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="fullMenu">
            <ul className="navbar-nav mr-auto mt-2 mt-lg-0 navbar-rtl">
              <li className="nav-item active">
                <a
                  className="nav-link text-dark font-weight-bold"
                  href="https://google.com"
                >
                  صفحه اصلی
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link text-dark" href="https://google.com">
                  نمایندگی های فروش
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link text-dark" href="https://google.com">
                  درباره ما
                </a>
              </li>
              <li className="nav-item">
                <button
                  className="btn my-2 my-sm-0 btn-view-cart text-dark font-weight-bold"
                  type="submit"
                >
                  مشاهده سبد خرید
                  <img src="cart.png" width="25px" alt="Cart" />
                </button>
              </li>
            </ul>
          </div>
        </nav>
        <ProductList filtered_products={this.state.filtered_products} />
      </div>
    );
  }
}

export default App;
