import React from "react";

const SearchBar = (props) => {
  return (
    <div className="container search-bar  d-flex flex-row justify-content-start">
      <input
        className="form-control mr-sm-2"
        id="search-field"
        type="search"
        placeholder="جستجو"
        aria-label="Search"
      ></input>
      <button
        className="btn btn-success text-dark btn-outline-success my-2 my-sm-0 font-weight-bold"
        id="btn-search"
        type="submit"
        onClick={props.onSearch}
      >
        جستجو در محصولات
      </button>
    </div>
  );
};

export default SearchBar;
